.mistake {
  background-image: url("../img/404-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 253px 0 206px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title {
    font-family: 'mts-ultraextended';
    font-size: 250px;
    line-height: 100%;
    text-transform: uppercase;
    color: #AFC82E;
  }
  &__subtitle {
    font-size: 40px;
    line-height: 140%;
    color: #fff;
    margin-top: 46px;
  }
  &__btns {
    justify-content: center;
    margin-top: 64px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
  }
}

@media screen and (max-width: 992px) {
  .mistake {
    padding: 249px 0 150px 0;
    &__title {
      font-size: 178px;
    }
    &__btns {
      width: 100%;
    }
    &__btn {
      flex-grow: 1;
    }
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    padding-top: 220px;
    &__title {
      font-size: 90px;
    }
    &__subtitle {
      font-size: 24px;
      margin-top: 32px;
    }
    &__btns {
      margin-top: 40px;
    }
    &__btn {
      height: 44px;
      span {
        font-size: 14px;
      }
    }
    &__btn2 {
      width: 44px;
      height: 44px;
      svg {
        width: 18px;
        height: auto;
      }
    }
  }
}