.vacancies {
  padding-bottom: 70px;
  &__title {
    border-bottom: 1px solid #D9D9D9;
    padding-bottom: 36px;
    color: #AFC82E;
  }
  &__wrap {
    background: #fff;
    border-radius: 32px;
    padding: 32px;
    margin-top: 36px;
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
    padding: 24px 0;
    &:not(:first-child) {
      border-top: 1px solid #D9D9D9;
    }
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      padding-bottom: 0;
    }
  }
  &__subgroup {
    max-width: 837px;
  }
  &__text {
    margin-top: 24px;
    p {
      font-size: 18px;
      line-height: 140%;
      color: #7A7A7A;
    }
  }
  &__nav {
    margin-top: 24px;
    &-title {
      font-size: 20px;
      line-height: 140%;
      font-weight: 700;
      margin-bottom: 16px;
    }
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    li {
      position: relative;
      padding-left: 15px;
      font-size: 18px;
      line-height: 140%;
      &:after {
        content: '';
        position: absolute;
        top: 10px;
        left: 0;
        width: 4px;
        height: 4px;
        background: #282827;
        border-radius: 50%;
      }
    }
  }
  &__btns {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 12px;
    &:hover {
      .vacancies__btn {
        color: #AFC82E;
      }
      .vacancies__btn2 {
        background: #AFC82E;
        border-color: #AFC82E;
        svg path {
          fill: #fff;
        }
      }
    }
  }
  &__btn {
    background: transparent;
    padding: 0;
    font-size: 18px;
    line-height: 140%;
    text-decoration: unset !important;
    &:hover {
      color: #AFC82E;
    }
  }
  &__btn2 {
    width: 36px;
    height: 36px;
    flex-shrink: 0;
  }
}

@media screen and (max-width: 992px) {
  .vacancies {
    padding-bottom: 40px;
    &__title {
      border-bottom: unset;
      padding-bottom: 0;
    }
    &__wrap {
      padding: 24px;
      margin-top: 24px;
    }
    &__group {
      flex-direction: column;
    }
    &__subgroup {
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .vacancies {
    padding-bottom: 32px;
    .container {
      padding: 0;
    }
    &__title {
      padding: 0 20px;
    }
    &__wrap {
      border-radius: 0;
      padding: 24px 20px;
    }
    &__subtitle {
      font-size: 18px;
    }
    &__text {
      margin-top: 16px;
      p {
        font-size: 14px;
      }
    }
    &__nav {
      margin-top: 16px;
      &-title {
        font-size: 18px;
      }
    }
    ul {
      li {
        font-size: 14px;
        &:after {
          top: 7px;
        }
      }
    }
    &__btn {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 359px) {
  .vacancies {
    &__title {
      padding: 0 15px;
    }
    &__wrap {
      padding: 24px 15px;
    }
  }
}