.certificates-main {
  padding-bottom: 140px;
  &__title {
    border-bottom: 1px solid #D9D9D9;
    padding-bottom: 36px;
    color: #AFC82E;
  }
  &__wrap {
    background: #fff;
    border-radius: 32px;
    padding: 32px;
    margin-top: 36px;
  }
  &__group {
    padding: 24px 0;
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      padding-bottom: 0;
    }
    &:not(:first-child) {
      border-top: 1px solid #D9D9D9;
    }
  }
  &__logo {
    margin-bottom: 24px;
    img {
      max-width: 100%;
    }
  }
  &__subtitle {
    margin-bottom: 24px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(9, calc(100% / 9 - 192px / 9));
    gap: 24px;
  }
}
.certificates-main__card {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 16px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    &:after {
      opacity: 1;
    }
    .increase {
      opacity: 1;
    }
  }
  &-img {
    img {
      width: 100%;
      display: block;
      border-radius: 16px;
    }
  }
  .increase {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 64px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .certificates-main {
    &__cards {
      grid-template-columns: repeat(6, calc(100% / 6 - 120px / 6));
    }
  }
}

@media screen and (max-width: 992px) {
  .certificates-main {
    padding-bottom: 80px;
    &__title {
      border-bottom: unset;
      padding-bottom: 0;
      font-size: 32px;
    }
    &__wrap {
      margin-top: 24px;
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 130px / 3));
      gap: 24px 65px;
    }
  }
}

@media screen and (max-width: 576px) {
  .certificates-main {
    padding-bottom: 64px;
    &__title {
      font-size: 24px;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
    }
    &__subtitle {
      font-size: 18px;
    }
  }
}