.portfolio {
  padding-bottom: 80px;
  overflow: hidden;
  &--2 {
    padding: 70px 0 140px;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 16px;
    margin-bottom: 64px;
  }
  &__title {
    max-width: 874px;
    color: #AFC82E;
  }
  &__text {
    max-width: 732px;
    p {
      font-size: 20px;
      line-height: 140%;
      color: #7A7A7A;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 64px / 3));
    gap: 48px 32px;
    border-top: 1px solid #D9D9D9;
    padding-top: 36px;
    margin-top: 36px;
  }
  .all-cards {
    .project-card:nth-child(9) ~ .project-card {
      display: none;
    }
  }
  .all-cards--active {
    .project-card:nth-child(9) ~ .project-card {
      display: flex;
    }
  }
  .project-card {
    &__group {
      margin: 0 0 24px 0;
    }
    &__items {
      top: 0;
      right: 0;
      left: unset;
      bottom: unset;
      gap: 8px;
      padding-left: 20px;
    }
    &__top {
      order: 1;
      flex-wrap: wrap;
      align-items: start;
      gap: 24px 8px;
    }
    &__title {
      font-family: 'mts-ultraextended';
      text-transform: uppercase;
      text-decoration: unset !important;
      &:hover {
        color: #AFC82E;
      }
    }
    &__text {
      margin-top: 12px;
    }
  }
  .show-more {
    margin-top: 32px;
  }
  .pagination {
    margin-top: 32px;
  }
  &__slider {
    margin-top: 64px;
    .swiper {
      overflow: unset;
    }
  }
  .tab__content2 {
    display: none;
  }
  .tab__content2--active {
    display: block;
  }
}
.portfolio-main {
  padding: 0 0 70px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 48px;
  }
  &__left {
    width: 58.5%;
    overflow: hidden;
  }
  &__right {
    width: 41.95%;
  }
  &__images {
    .swiper-slide {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.41);
        border-radius: 32px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
      &:hover {
        &:after {
          opacity: 1;
        }
        .increase {
          opacity: 1;
        }
      }
    }
    @media screen and (min-width: 993px) {
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(2, calc(50% - 32px / 2));
        gap: 32px;
      }
      .swiper-slide {
        width: 100% !important;
      }
      .big--img {
        grid-column: 1/3;
        .portfolio-main__img {
          padding-bottom: 59.48%;
        }
      }
    }
  }
  &__img {
    display: block;
    padding-bottom: 100%;
    outline: unset !important;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
    }
  }
  .increase {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    img {
      max-width: 100%;
    }
  }
  &__group {
    position: sticky;
    top: 0;
    left: 0;
  }
  &__title {
    color: #AFC82E;
  }
  &__title--mob {
    display: none;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 24px;
    p {
      font-size: 20px;
      line-height: 140%;
      color: #7A7A7A;
    }
    strong {
      color: #282827;
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-top: 24px;
  }
  &__item {
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #282827;
    border-radius: 37px;
    padding: 3px 15px;
    font-size: 16px;
    line-height: 140%;
  }
  &__btns {
    margin-top: 40px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
  }
}

@media screen and (max-width: 1200px) {
  .portfolio {
    .project-card {
      &__img {
        padding-bottom: 75%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .portfolio {
    &--2 {
      padding: 40px 0 80px;
    }
    &__top {
      flex-direction: column;
      margin-bottom: 32px;
    }
    &__title {
      max-width: 100%;
    }
    &__text {
      max-width: 100%;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
      border-top: unset;
      padding-top: 0;
      margin-top: 40px;
    }
    .all-cards {
      .project-card:nth-child(6) ~ .project-card {
        display: none;
      }
    }
    .all-cards--active {
      .project-card:nth-child(6) ~ .project-card {
        display: flex;
      }
    }
    .project-card {
      &__img ~ .project-card__img:nth-child(1) {
        display: none;
      }
      .hover__tab-head,
      .hover__pagination {
        display: none !important;
      }
      &__title {
        font-size: 20px;
      }
    }
    &__slider {
      margin-top: 40px;
    }
  }
  .portfolio-main {
    padding: 0 0 40px 0;
    overflow: hidden;
    &__inner {
      flex-direction: column;
      gap: 24px;
    }
    &__left {
      width: 100%;
      overflow: unset;
    }
    &__right {
      width: 100%;
    }
    &__images {
      margin-top: 24px;
      .swiper {
        overflow: unset;
      }
    }
    &__img {
      padding-bottom: 80%;
    }
    &__title {
      display: none;
    }
    &__title--mob {
      display: block;
    }
    &__text {
      margin-top: 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .portfolio {
    padding-bottom: 64px;
    &--2 {
      padding: 32px 0 64px;
    }
    &__top {
      margin-bottom: 0;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__cards {
      grid-template-columns: 100%;
      margin-top: 24px;
    }
    .all-cards {
      .project-card:nth-child(5) ~ .project-card {
        display: none;
      }
    }
    .all-cards--active {
      .project-card:nth-child(5) ~ .project-card {
        display: flex;
      }
    }
    .project-card {
      &__img {
        min-height: 255px;
      }
    }
    &__slider {
      margin-top: 32px;
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        grid-template-columns: 100%;
        gap: 24px;
      }
      .swiper-slide {
        margin: 0 !important;
      }
    }
  }
  .portfolio-main {
    padding: 0 0 32px 0;
    &__img {
      border-radius: 24px;
      padding-bottom: 87%;
      img {
        border-radius: 24px;
      }
    }
    &__text {
      gap: 16px;
      p {
        font-size: 14px;
      }
    }
    &__items {
      margin-top: 16px;
    }
    &__item {
      height: 36px;
      font-size: 14px;
    }
    &__btns {
      margin-top: 24px;
    }
    &__btn {
      height: 44px;
      flex-grow: 1;
      span {
        font-size: 14px;
      }
    }
    &__btn2 {
      width: 44px;
      height: 44px;
      svg {
        width: 16px;
        height: auto;
      }
    }
  }
}