.privacy {
  padding-bottom: 140px;
  &__title {
    border-bottom: 1px solid #D9D9D9;
    padding-bottom: 36px;
    color: #AFC82E;
  }
  &__wrap {
    background: #fff;
    border-radius: 32px;
    padding: 32px;
    margin-top: 36px;
  }
  &__text {
    max-width: 1033px;
    h3 {
      font-size: 24px;
      line-height: 120%;
      margin-bottom: 24px;
    }
    p {
      font-size: 18px;
      line-height: 140%;
      color: #7A7A7A;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
    ul {
      margin-bottom: 16px;
      li {
        position: relative;
        padding-left: 15px;
        font-size: 18px;
        line-height: 140%;
        color: #7A7A7A;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 4px;
          height: 4px;
          background: #7A7A7A;
          border-radius: 50%;
        }
      }
    }
    hr {
      width: 0;
      height: 16px;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
  }
}

@media screen and (max-width: 992px) {
  .privacy {
    padding-bottom: 80px;
    &__title {
      border-bottom: unset;
      padding-bottom: 0;
      font-size: 32px;
    }
    &__wrap {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .privacy {
    padding-bottom: 64px;
    &__wrap {
      border-radius: 24px;
      padding: 16px;
    }
    &__title {
      font-size: 24px;
    }
    &__text {
      h3 {
        font-size: 18px;
        margin-bottom: 16px;
      }
      p {
        font-size: 14px;
      }
      ul {
        li {
          font-size: 14px;
        }
      }
    }
  }
}