.about-main {
  padding-bottom: 70px;
  &__title {
    color: #AFC82E;
  }
  .services-main__inner {
    border-bottom: unset;
    padding-bottom: 0;
    margin-top: 64px;
  }
  .services-main__left {
    border-radius: 32px;
    padding: 32px;
  }
  .services-main__right {
    order: -1;
  }
  .services-main__text {
    gap: 24px;
    margin-top: 24px;
    p {
      font-size: 18px;
    }
  }
  .services-main__img {
    padding-bottom: 77%;
  }
}
.contacts {
  padding: 70px 0 140px;
  &__items {
    display: grid;
    grid-template-columns: 1.35fr 0.65fr;
    gap: 32px;
    margin-top: 64px;
  }
  &__item {
    background: #fff;
    border-radius: 32px;
    padding: 32px;
    &-top {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: 8px 16px;
      margin-bottom: 32px;
    }
    &-title {
      font-size: 24px;
      line-height: 140%;
      font-weight: 700;
    }
    .tabs-head {
      display: flex;
      align-items: center;
      gap: 16px;
    }
    .tab2 {
      font-size: 18px;
      line-height: 140%;
      color: #777777;
      cursor: pointer;
    }
    .tab2--active {
      color: #AFC82E;
    }
    &-text {
      font-size: 20px;
      line-height: 140%;
      color: #AFC82E;
    }
    &-phone,
    &-email {
      display: block;
      font-size: 20px;
      line-height: 140%;
      text-decoration: unset !important;
      margin-top: 20px;
      &:hover {
        color: #AFC82E;
      }
    }
    &-location {
      font-size: 20px;
      line-height: 120%;
    }
    .tab__content2 {
      display: none;
    }
    .tab__content2--active {
      display: block;
    }
    &-text2 {
      font-size: 18px;
      line-height: 140%;
      color: #7A7A7A;
      margin-top: 20px;
    }
  }
  &__subitems {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 64px / 2));
    gap: 64px;
  }
  &__subitem {
    position: relative;
    &:nth-child(odd) {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: -32px;
        width: 1px;
        height: 100%;
        background: #D9D9D9;
        pointer-events: none;
      }
    }
  }
  &__wrap {
    position: relative;
    padding: 32px;
    margin-top: 32px;
  }
  .form-wrap {
    position: relative;
    z-index: 2;
  }
  .map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-inner-panes {
      border-radius: 32px;
    }
    .ymaps-2-1-79-map {
      position: absolute;
      top: 0;
      left: 0;
    }
    .ymaps-2-1-79-controls__control_toolbar {
      display: none;
    }
  }
}
.contacts-main {
  padding-top: 0 !important;
  .contacts__title {
    border-bottom: 1px solid #D9D9D9;
    padding-bottom: 36px;
    color: #AFC82E;
  }
  .contacts__items {
    margin-top: 36px;
  }
}
.staff {
  padding-bottom: 70px;
  &__title {
    color: #AFC82E;
  }
  &__img {
    position: relative;
    min-height: 488px;
    padding-bottom: 46.21%;
    margin-top: 48px;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.connection2 {
  padding: 70px 0 140px;
  &__inner {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 120px / 2));
    gap: 120px;
    background: #fff;
    border-radius: 48px;
    padding: 80px;
  }
  &__desc {
    display: flex;
    flex-direction: column;
  }
  &__title {
    color: #AFC82E;
  }
  &__text {
    margin-top: 20px;
    margin-bottom: auto;
    p {
      font-size: 20px;
      line-height: 140%;
    }
  }
  &__btns {
    margin-top: 32px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
  }
  .form-wrap {
    max-width: 100%;
    background: #F9F9F9;
  }
  .form-title {
    text-align: center;
    color: #AFC82E;
  }
  .form {
    gap: 16px;
    margin-top: 16px;
  }
  .form-grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 8px / 2));
    gap: 8px;
  }
  .form-group {
    input {
      background: transparent;
    }
  }
  .form-check {
    margin-top: 0;
  }
  .form-btn {
    margin-top: 16px;
  }
  .file {
    position: relative;
    top: unset;
    right: unset;
    width: 100%;
    min-height: 254px;
    border: 1px dashed #AFC82E;
    border-radius: 32px;
    padding: 16px;
    &-label {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
    }
    &-text {
      font-size: 14px;
      line-height: 140%;
      font-weight: 400;
      color: #7A7A7A;
      margin-top: 15px;
      span {
        color: #AFC82E;
      }
    }
    &-info {
      position: absolute;
      left: 50%;
      bottom: 24px;
      transform: translate(-50%, 0);
      min-width: 295px;
      height: 64px;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      border: 1px solid #AFC82E;
      border-radius: 12px;
      padding: 5px 24px;
      opacity: 0;
      pointer-events: none;
    }
    &-icon {
      width: 20px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      gap: 10px;
      img {
        width: 100%;
      }
    }
    &-icon2 {
      width: 14px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      margin-left: 30px;
      img {
        width: 100%;
      }
    }
    &-text2 {
      font-size: 16px;
      line-height: 115%;
      color: #AFC82E;
    }
  }
  .file--active {
    min-height: 333px !important;
    padding-bottom: 103px !important;
    .file-info {
      opacity: 1;
      pointer-events: auto;
    }
  }
  .select-head {
    background: transparent;
  }
}

@media screen and (max-width: 1400px) {
  .connection2 {
    &__inner {
      grid-template-columns: repeat(2, calc(50% - 32px / 2));
      gap: 32px;
      padding: 32px;
    }
    @media screen and (min-width: 993px) {
      .form-grid {
        grid-template-columns: 100%;
        gap: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .about-main {
    padding-bottom: 40px;
    &__title {
      font-size: 38px;
    }
    .services-main__inner {
      margin-top: 24px;
    }
    .services-main__img {
      padding-bottom: 77.7%;
    }
    .services-main__subtitle {
      font-size: 20px;
    }
  }
  .contacts {
    padding: 40px 0 80px;
    &__items {
      grid-template-columns: 100%;
      gap: 24px;
      margin-top: 48px;
    }
    &__item {
      &-text2 {
        max-width: 513px;
      }
    }
    &__subitems {
      grid-template-columns: 100%;
    }
    &__subitem {
      position: relative;
      &:nth-child(odd) {
        &:after {
          display: none;
        }
      }
      &:not(:last-child) {
        &:after {
          content: '';
          position: absolute;
          width: 100%;
          height: 1px;
          top: unset;
          right: 0;
          bottom: -32px;
          display: block;
          background: #D9D9D9;
          pointer-events: none;
        }
      }
    }
    &__wrap {
      padding-bottom: 74%;
    }
    .form-wrap {
      display: none;
    }
  }
  .contacts-main {
    .contacts__title {
      border-bottom: unset;
      padding-bottom: 0;
    }
    .contacts__items {
      margin-top: 24px;
    }
    .contacts__wrap {
      padding: 0;
    }
    .form-wrap {
      display: block;
      margin-top: 48px;
    }
    .map-wrap {
      position: relative;
      min-height: 248px;
      padding-bottom: 78%;
    }
  }
  .staff {
    padding-bottom: 40px;
    &__title {
      font-size: 38px;
    }
    &__img {
      margin-top: 24px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
  .connection2 {
    padding: 40px 0 80px;
    &__inner {
      grid-template-columns: 100%;
    }
    .file {
      min-height: 303px;
      &-title,
      &-text {
        max-width: 240px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .about-main {
    &__title {
      span {
        display: none;
      }
    }
  }
  .staff {
    &__img {
      min-height: 251px;
      img {
        border-radius: 24px;
      }
    }
  }
  .connection2 {
    .form-grid {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .about-main {
    padding-bottom: 32px;
    &__title {
      font-size: 24px;
    }
    .services-main__left {
      border-radius: 24px;
      padding: 32px 16px;
    }
    .services-main__text {
      gap: 20px;
      margin-top: 16px;
      p {
        font-size: 14px;
      }
    }
    .services-main__subtitle {
      font-size: 16px;
    }
    .services-main__img {
      min-height: 250px;
      padding-bottom: 70%;
    }
  }
  .contacts {
    padding: 32px 0 64px;
    &__items {
      gap: 16px;
      margin-top: 32px;
    }
    &__item {
      border-radius: 24px;
      padding: 16px;
      &-top {
        margin-bottom: 16px;
      }
      &-title {
        font-size: 18px;
      }
      &-text {
        font-size: 14px;
      }
      &-phone,
      &-email {
        font-size: 14px;
      }
      &-location {
        font-size: 14px;
      }
      &-text2 {
        font-size: 14px;
      }
    }
    &__subitems {
      gap: 32px;
    }
    &__subitem {
      &:not(:last-child) {
        &:after {
          bottom: -16px;
        }
      }
    }
    &__wrap {
      min-height: 414px;
    }
  }
  .contacts-main {
    .contacts__wrap {
      margin-top: 16px;
    }
    .form-wrap {
      margin-top: 40px;
    }
  }
  .staff {
    padding-bottom: 32px;
    &__title {
      font-size: 24px;
    }
  }
  .connection2 {
    padding: 32px 0 80px;
    .container {
      padding: 0;
    }
    &__inner {
      gap: 24px;
      border-radius: 0;
      padding: 32px 16px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__btn {
      height: 44px;
      flex-grow: 1;
      span {
        font-size: 14px;
      }
    }
    &__btn2 {
      width: 44px;
      height: 44px;
      svg {
        width: 18px;
        height: auto;
      }
    }
    .form-wrap {
      padding: 16px 8px;
    }
    .form-title {
      text-align: start;
    }
    .form-group {
      input {
        height: 58px;
        padding: 0 20px;
        font-size: 16px;
      }
    }
    .form-btn {
      height: 64px;
      span {
        font-size: 20px;
      }
    }
    .file {
      min-height: 140px;
      border-radius: 12px;
      padding: 0;
      &-title {
        max-width: 100%;
        font-size: 16px;
      }
      &-text {
        max-width: 100%;
        font-size: 14px;
      }
      &-info {
        width: 100%;
        min-width: unset;
      }
    }
    .select-head {
      height: 58px;
    }
  }
}