.news {
  padding: 0 0 80px 0;
  &--2 {
    padding: 70px 0 140px;
  }
  &__top {
    margin-bottom: 64px;
  }
  &__title {
    color: #AFC82E;
    .br1 {
      display: none;
    }
  }
  &__text {
    display: none;
    margin-top: 16px;
    p {
      font-size: 20px;
      line-height: 140%;
      color: #7A7A7A;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 64px / 3));
    gap: 48px 32px;
    border-top: 1px solid #D9D9D9;
    padding-top: 36px;
    margin-top: 36px;
  }
  .all-cards {
    .news-card:nth-child(9) ~ .news-card {
      display: none;
    }
  }
  .all-cards--active {
    .news-card:nth-child(9) ~ .news-card {
      display: flex;
    }
  }
  .show-more {
    margin-top: 32px;
  }
  .pagination {
    margin-top: 32px;
  }
  &__slider {
    margin-top: 64px;
    .swiper {
      overflow: unset;
    }
  }
  .tab__content2 {
    display: none;
  }
  .tab__content2--active {
    display: block;
  }
}
.news-card {
  display: flex;
  flex-direction: column;
  &__img {
    position: relative;
    display: block;
    border-radius: 32px;
    padding-bottom: 77%;
    overflow: hidden;
    &:hover {
      img {
        transform: scale(1.2);
      }
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
      transition: all 0.5s;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 24px;
  }
  &__text {
    font-size: 24px;
    line-height: 140%;
    text-decoration: unset !important;
    color: #282827 !important;
    margin-bottom: auto;
  }
  &__date {
    display: flex;
    align-items: center;
    gap: 4px;
    margin-top: 12px;
    i {
      width: 16px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 140%;
    }
  }
}
.news-main {
  padding: 48px 0 70px;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 32px;
  }
  &__group {
    width: 50%;
  }
  .sticky-group {
    position: sticky;
    top: 0;
    left: 0;
    min-height: 677px;
    display: flex;
    flex-direction: column;
  }
  &__title {
    margin-top: 24px;
  }
  &__text {
    margin-top: 24px;
    margin-bottom: auto;
    p {
      font-size: 20px;
      line-height: 140%;
      color: #7A7A7A;
    }
  }
  &__subgroup {
    display: flex;
    align-items: center;
    gap: 24px;
    margin-top: 24px;
  }
  &__date {
    display: flex;
    align-items: center;
    gap: 4px;
    i {
      width: 16px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 140%;
    }
  }
  &__user {
    position: relative;
    padding-left: 15px;
    font-size: 16px;
    line-height: 140%;
    color: #7A7A7A;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 4px;
      height: 4px;
      background: #7A7A7A;
      border-radius: 50%;
    }
  }
  &__img {
    position: relative;
    border-radius: 32px;
    padding-bottom: 77%;
    margin-bottom: 70px;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
    }
  }
  &__text2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
    p {
      font-size: 20px;
      line-height: 140%;
    }
  }
  &__img2 {
    position: relative;
    padding-bottom: 51.2%;
    margin: 40px 0;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
    }
  }
  .info2__right {
    width: 100%;
    max-width: 425px;
    border-radius: 24px;
    padding: 24px;
    margin-top: 24px;
  }
}


@media screen and (max-width: 992px) {
  .news {
    &--2 {
      padding: 40px 0 80px;
    }
    &__top {
      margin-bottom: 32px;
    }
    &__title {
      .br1 {
        display: block;
      }
    }
    &__text {
      display: block;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
      border-top: unset;
      padding-top: 0;
      margin-top: 40px;
    }
    .all-cards {
      .news-card:nth-child(6) ~ .news-card {
        display: none;
      }
    }
    .all-cards--active {
      .news-card:nth-child(6) ~ .news-card {
        display: flex;
      }
    }
    &__slider {
      margin-top: 40px;
    }
  }
  .news-card {
    &__img {
      padding-bottom: 75%;
    }
    &__text {
      font-size: 18px;
    }
    &__date {
      margin-top: 24px;
    }
  }
  .news-main {
    padding: 48px 0 40px;
    &__inner {
      flex-direction: column;
      gap: 48px;
    }
    &__group {
      width: 100%;
    }
    .sticky-group {
      min-height: unset;
    }
    &__img {
      margin-bottom: 48px;
    }
    &__text {
      margin-top: 16px;
    }
    &__text2 {
      gap: 24px;
    }
    &__img2 {
      padding-bottom: 65%;
      margin: 48px 0;
    }
    .info2__right {
      max-width: 100%;
      margin-top: 16px;
    }
  }
}

@media screen and (max-width: 576px) {
  .news {
    padding-bottom: 64px;
    &--2 {
      padding: 32px 0 64px;
    }
    &__top {
      margin-bottom: 0;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__cards {
      grid-template-columns: 100%;
      margin-top: 24px;
    }
    .all-cards {
      .news-card:nth-child(3) ~ .news-card {
        display: none;
      }
    }
    .all-cards--active {
      .news-card:nth-child(3) ~ .news-card {
        display: flex;
      }
    }
    &__slider {
      margin-top: 32px;
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        grid-template-columns: 100%;
        gap: 24px;
      }
      .swiper-slide {
        margin: 0 !important;
      }
    }
  }
  .news-card {
    &__img {
      min-height: 254px;
    }
  }
  .news-main {
    padding: 32px 0;
    &__title {
      font-size: 20px;
      wbr {
        display: none;
      }
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__text2 {
      p {
        font-size: 14px;
      }
    }
    &__img {
      margin-bottom: 24px;
    }
    &__img2 {
      min-height: 258px;
      margin: 24px 0;
    }
    .info2__right {
      border-radius: 16px;
      padding: 16px;
    }
    .info2__title {
      display: block !important;
      font-size: 18px;
    }
    .info2__links {
      margin-top: 16px;
    }
    .info2__link {
      span {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 374px) {
  .news-main {
    &__title {
      font-size: 18px;
    }
  }
}