.header {
  position: relative;
  z-index: 10;
  margin-top: 31px;
  &__inner {
    border-top: 1px solid #C6C6C6;
    border-bottom: 1px solid #C6C6C6;
    padding: 13.5px 0;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }
  &__logo {
    width: 394px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 40px;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 40px;
    li a {
      font-size: 20px;
      line-height: 120%;
      text-decoration: unset !important;
      &:hover {
        color: #AFC82E;
      }
    }
  }
  &-item {
    position: relative;
    &--active {
      .header-item__show {
        i {
          transform: rotate(-180deg);
        }
      }
      .header-item__hidden {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__show {
      display: flex;
      align-items: center;
      gap: 12px;
      i {
        width: 14px;
        display: flex;
        align-items: center;
        transition: all 0.225s;
        svg {
          width: 100%;
          height: auto;
        }
      }
    }
    &__hidden {
      position: absolute;
      top: 100%;
      left: 0;
      transform: translateY(-10px);
      width: auto;
      min-width: 200px;
      background: #fff;
      border-radius: 8px;
      padding: 16px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.225s;
    }
    &__list {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
    }
  }
  &__languages {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__language {
    background: transparent;
    padding: 0;
    font-size: 20px;
    line-height: 120%;
    text-decoration: unset !important;
    color: #7A7A7A;
    cursor: pointer;
    &:hover {
      color: #7A7A7A;
    }
  }
  .language-active {
    color: #AFC82E;
  }
  &__mob {
    display: none;
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__inner {
      padding: 16px 0;
    }
    &__wrap {
      gap: 30px;
    }
    &__logo {
      width: 300px;
    }
    &__group {
      gap: 30px;
    }
    &__list {
      gap: 28px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__logo {
      width: 250px;
    }
    &__list {
      li a {
        font-size: 16px;
      }
    }
    &__language {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 1050px) {
  .header {
    &__wrap {
      gap: 12px;
    }
    &__group {
      gap: 24px;
    }
    &__list {
      gap: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    position: relative;
    margin-top: 32px;
    &:after {
      content: '';
      position: absolute;
      z-index: 9;
      top: -32px;
      left: 0;
      width: 100%;
      height: 32px;
      background: #F9F9F9;
    }
    &__inner {
      border: none;
      padding: 0;
    }
    &__wrap {
      position: fixed;
      z-index: 8;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: start;
      align-items: start;
      gap: 64px;
      background: #F9F9F9;
      padding: 200px 32px 64px 32px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      .header__logo {
        display: none;
      }
    }
    &__logo {
      width: 394px;
    }
    &__list {
      width: 100%;
      flex-direction: column;
      align-items: start;
      gap: 64px;
      li {
        width: 100%;
      }
      li a {
        font-family: 'mts-ultraextended';
        font-size: 38px;
        text-transform: uppercase;
      }
    }
    &-item {
      &--active {
        .header-item__hidden {
          display: block;
        }
      }
      &__show {
        justify-content: space-between;
        i {
          width: 28px;
        }
      }
      &__hidden {
        position: static;
        transform: unset;
        display: none;
        background: transparent;
        padding: 0;
        margin-top: 24px;
        opacity: 1;
        pointer-events: auto;
      }
      &__list {
        gap: 24px;
        li a {
          font-size: 20px;
        }
      }
    }
    &__group {
      flex-grow: 1;
      flex-direction: column;
      align-items: start;
      gap: 64px;
    }
    &__languages {
      flex-grow: 1;
      align-items: flex-end;
      gap: 40px;
    }
    &__language {
      font-family: 'mts-ultraextended';
      font-size: 38px;
      text-transform: uppercase;
    }
    &__mob {
      position: relative;
      z-index: 10;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      background: #F9F9F9;
      border-top: 1px solid #C6C6C6;
      border-bottom: 1px solid #C6C6C6;
      padding: 13.5px 0;
    }
  }
  .burger {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    &__text {
      span {
        font-size: 24px;
        line-height: 120%;
        color: #AFC82E;
      }
      span i {
        font-style: normal;
      }
      span:nth-child(2) {
        display: none;
      }
    }
    &__btn {
      position: relative;
      width: 46.2px;
      height: 14px;
      span {
        position: absolute;
        left: 0;
        width: 100%;
        height: 2.8px;
        background: #AFC82E;
        transition: all 0.125s;
      }
      span:nth-child(1) {
        top: 0;
      }
      span:nth-child(2) {
        bottom: 0;
      }
    }
  }
  .burger--active {
    .burger__text {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
    }
    .burger__btn {
      span:nth-child(1) {
        top: 5px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        bottom: 5px;
        transform: rotate(45deg);
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    margin-top: 20px;
    &:after {
      top: -21px;
      height: 21px;
    }
    &__wrap {
      gap: 40px;
    }
    &__logo {
      width: 300px;
    }
    &__list {
      gap: 40px;
      li a {
        font-size: 20px;
      }
    }
    &-item {
      &__show {
        i {
          width: 14px;
        }
      }
      &__list {
        gap: 12px;
        li a {
          font-size: 14px;
        }
      }
    }
    &__group {
      gap: 40px;
    }
    &__language {
      font-size: 20px;
    }
  }
  .burger {
    &__text {
      span i {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__wrap {
      padding: 132px 20px 40px 20px;
    }
    &__logo {
      width: 218px;
    }
  }
  .burger {
    &__text {
      span {
        font-size: 16px;
      }
    }
    &__btn {
      width: 33px;
      height: 10px;
    }
  }
  .burger--active {
    .burger__btn {
      span:nth-child(1) {
        top: 3px;
      }
      span:nth-child(2) {
        bottom: 4px;
      }
    }
  }
}

@media screen and (max-width: 374px) {
  .header {
    &__logo {
      width: 180px;
    }
  }
  .burger {
    &__text {
      span {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__wrap {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}