.main {
  padding: 72px 0 70px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 32px;
  }
  &__title {
    max-width: 874px;
    font-size: 58px;
    line-height: 121%;
    text-transform: uppercase;
    span {
      color: #AFC82E;
    }
  }
  &__group {
    max-width: 732px;
    width: 100%;
  }
  &__text {
    max-width: 484px;
    p {
      font-size: 20px;
      line-height: 140%;
      color: #7A7A7A;
    }
  }
  &__btns {
    margin-top: 24px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
  }
  &__slider {
    margin-top: 64px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
  }
}
.main-slide {
  position: relative;
  border-radius: 32px;
  &:hover {
    .main-slide__btn {
      opacity: 1;
      pointer-events: auto;
    }
    .main-slide__img {
      img {
        transform: scale(1.125);
      }
    }
  }
  &__img {
    position: relative;
    min-height: 547px;
    padding-bottom: 46.206%;
    border-radius: 32px;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
      transition: all 0.5s;
    }
  }
  &__top {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    display: flex;
    align-items: stretch;
    gap: 17px;
    padding: 48px;
  }
  &__info {
    background: rgba(255, 255, 255, 0.01);
    backdrop-filter: blur(12.7px);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 37px;
    padding: 11px 16px;
    font-size: 20px;
    line-height: 140%;
    color: #fff;
  }
  &__btn {
    position: absolute;
    z-index: 3;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 167px;
    height: 167px;
    text-align: center;
    background: rgba(255, 255, 255, 0.01);
    backdrop-filter: blur(12.7px);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    padding: 5px;
    opacity: 0;
    pointer-events: none;
    &:hover {
      background: rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(20px);
    }
    span {
      font-size: 20px;
      line-height: 140%;
      color: #fff;
    }
  }
  &__bottom {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    padding: 12px 48px 56px 48px;
  }
  &__title {
    color: #fff;
  }
  &__btn2 {
    position: static;
    width: auto;
    height: 52px;
    display: inline-flex;
    gap: 8px;
    background: rgba(255, 255, 255, 0.01);
    backdrop-filter: blur(12.7px);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 37px;
    padding: 4px 17.5px;
    color: #fff !important;
    margin-top: 24px;
    &::after {
      display: none;
    }
    &:hover {
      background: rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(20px);
    }
    span {
      font-size: 20px;
      line-height: 140%;
    }
    i {
      width: 24px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
}
.advantages {
  padding: 70px 0 140px;
  .section-top {
    max-width: 1679px;
    &__group {
      max-width: 735px;
    }
    &__group2 {
      max-width: 620px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-top: 64px;
  }
  &__item {
    background: #fff;
    border-radius: 32px;
    padding: 48px 32px 32px 32px;
    &-icon {
      transform: rotate(45deg);
      width: 72px;
      height: 72px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: #AFC82E;
      border-radius: 20px;
      margin-left: 14px;
      img {
        transform: rotate(-45deg);
        max-width: 80%;
        max-height: 80%;
      }
      span {
        transform: rotate(-45deg);
        font-family: 'mts-ultraextended';
        font-size: 38px;
        line-height: 120%;
        color: #fff;
      }
    }
    &-desc {
      margin-top: 48px;
    }
    &-title {
      font-size: 24px;
      line-height: 140%;
      font-weight: 700;
    }
    &-text {
      font-size: 18px;
      line-height: 140%;
      color: #7A7A7A;
      margin-top: 24px;
    }
  }
  &__top {
    max-width: 991px;
  }
}
.advantages--2 {
  background-image: url("../img/advantages-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 140px;
  .advantages__title {
    color: #fff;
    span {
      color: #fff;
    }
  }
}
.advantages--3 {
  padding: 70px 0;
}
.services {
  background: #282827;
  padding: 139px 0;
  color: #fff;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 175px;
  }
  &__left {
    width: calc(100% - 908px);
  }
  &__right {
    position: relative;
    z-index: 2;
    width: 733px;
  }
  &__img {
    position: relative;
    padding-bottom: 137%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
    }
  }
  .tab__content {
    display: none;
  }
  .tab__content--active {
    display: block;
  }
  .section-top {
    &__btn {
      height: 38px;
      padding: 4px 15px;
      margin: 0;
      span {
        font-size: 16px;
      }
    }
    &__title {
      margin-top: 32px;
    }
    &__text {
      margin-top: 32px;
      p {
        color: #fff;
      }
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 16px;
    margin-top: 32px;
  }
  &__total {
    font-size: 18px;
    line-height: 140%;
  }
  &__total-number {
    font-size: 18px;
    line-height: 140%;
  }
  &__tabs-head {
    margin-top: 56px;
  }
  &__tab {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
    padding: 32px 0;
    cursor: pointer;
    &:last-child {
      padding-bottom: 12px;
    }
    &-desc {
      position: relative;
      z-index: 2;
    }
    &-title {
      font-size: 24px;
      line-height: 140%;
      font-weight: 700;
    }
    &-text {
      font-size: 18px;
      line-height: 140%;
      color: #D7D7D7;
      margin-top: 17px;
    }
    &-btn {
      position: relative;
      z-index: 2;
      width: 60px;
      height: 60px;
      flex-shrink: 0;
    }
  }
  .tab--active {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -175px;
      width: 20000px;
      height: 100%;
      background: #545451;
      pointer-events: none;
    }
    .services__tab-btn {
      background: #AFC82E;
      border-color: #AFC82E;
    }
  }
  &__btns {
    margin-top: 12px;
  }
  &__btn {
    height: 60px;
    flex-grow: 1;
    padding: 4px 16px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
    &:hover {
      background: #fff;
      svg path {
        fill: #282827;
      }
    }
    svg path {
      transition: all 0.3s;
    }
  }
}
