  }
}
.project-card {
  display: flex;
  flex-direction: column;
  &__top {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    padding-top: 4px;
  }
  &__area,
  &__date {
    font-size: 18px;
    line-height: 140%;
  }
  &__title {
    font-family: 'mts-ultraextended';
    text-transform: uppercase;
    text-decoration: unset !important;
    &:hover {
      color: #AFC82E;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 12px;
    text-decoration: unset !important;
    color: #282827 !important;
    &:hover {
      .project-card__btn {
        color: #AFC82E;
      }
      .project-card__btn2 {
        background: #AFC82E;
        border-color: #AFC82E;
        svg path {
          fill: #fff;
        }
      }
    }
  }
  &__btn {
    font-size: 18px;
    line-height: 140%;
    transition: all 0.3s;
    &:hover {
      color: #AFC82E;
    }
  }
  &__btn2 {
    width: 36px;
    height: 36px;
    flex-shrink: 0;
    transition: all 0.3s;
  }
  &__group {
    position: relative;
    margin-top: 24px;
  }
  &__img {
    position: relative;
    display: block;
    padding-bottom: 76%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
    }
  }
  &__items {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 16px;
    padding: 16px;
  }
  &__item {
    background: #fff;
    border-radius: 37px;
    padding: 8px 16px;
    font-size: 16px;
    line-height: 140%;
  }
  &__text {
    font-size: 16px;
    line-height: 140%;
    color: #7A7A7A;
  }
  &__text2 {
    max-height: 192px;
    margin-top: 24px;
    display: -webkit-box;
    -webkit-line-clamp:7;
    -webkit-box-orient: vertical;
    overflow: hidden;
    p {
      font-size: 18px;
      line-height: 140%;
      color: #7A7A7A;
    }
    p:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  .text-overflow--active {
    max-height: 100%;
    display: block;
    overflow: unset;
  }
  .show-more__btn {
    margin-top: 8px;
    span {
      font-size: 18px;
      line-height: 140%;
      cursor: pointer;
      transition: all 0.3s;
      &:hover {
        color: #AFC82E;
      }
    }
    span:nth-child(2) {
      display: none;
    }
  }
  .show-more__btn--active {
    span:nth-child(1) {
      display: none;
    }
    span:nth-child(2) {
      display: block;
    }
  }
  &__title2 {
    font-size: 18px;
    line-height: 140%;
    margin-top: 24px;
  }
}
