.wrapper {
  position: relative;
  overflow: hidden;
}
.btns {
  display: flex;
  align-items: center;
  gap: 8px;
}
.btn-black {
  background: #282827;
  border-radius: 58px;
  color: #fff !important;
  &:hover {
    background: #AFC82E;
  }
  span {
    line-height: 140%;
  }
}
.btn-black--outline {
  background: transparent;
  border: 1px solid #282827;
  border-radius: 36px;
  &:hover {
    background: #282827;
    color: #fff;
  }
  span {
    line-height: 140%;
  }
}
.btn-black--arrow {
  background: transparent;
  border: 1px solid #282827;
  border-radius: 50%;
  padding: 0;
  &:hover {
    background: #AFC82E;
    border-color: #AFC82E;
    svg path {
      fill: #fff;
    }
  }
  svg {
    max-width: 80%;
    max-height: 80%;
  }
  svg path {
    transition: all 0.3s;
  }
}
.btn-green {
  background: #AFC82E;
  border-radius: 58px;
  color: #fff !important;
  &:hover {
    background: #282827;
  }
  span {
    line-height: 140%;
  }
}
.btn-green--arrow {
  background: #AFC82E;
  border-radius: 50%;
  padding: 0;
  &:hover {
    background: #282827;
  }
  svg {
    max-width: 80%;
    max-height: 80%;
  }
}
.btn-white {
  background: #fff;
  border-radius: 58px;
  color: #282827;
  &:hover {
    background: #AFC82E;
    color: #fff;
  }
  span {
    line-height: 140%;
  }
}
.btn-white--outline {
  background: transparent;
  border: 1px solid #fff;
  border-radius: 36px;
  color: #fff;
  &:hover {
    background: #fff;
    color: #282827;
  }
  span {
    line-height: 140%;
  }
}
.btn-white--arrow {
  background: transparent;
  border: 1px solid #fff;
  border-radius: 50%;
  padding: 0;
  &:hover {
    background: #AFC82E;
    border-color: #AFC82E;
  }
  svg {
    max-width: 80%;
    max-height: 80%;
  }
}
.h1 {
  font-size: 58px;
  line-height: 120%;
  text-transform: uppercase;
}
.h2 {
  font-size: 38px;
  line-height: 120%;
  span {
    color: #AFC82E;
  }
}
.h3 {
  font-size: 24px;
  line-height: 120%;
}
.section-top {
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 32px;
  &__btn {
    height: 38px;
    display: inline-flex;
    padding: 4px 16px;
    margin-bottom: 32px;
    span {
      font-size: 16px;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 17px;
    p {
      font-size: 18px;
      line-height: 140%;
      color: #7A7A7A;
    }
    p b {
      color: #282827;
    }
  }
  &__items {
    display: inline-flex;
    align-items: center;
  }
  &__item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 20px;
    padding: 10px;
    &:nth-child(1) {
      z-index: 3;
    }
    &:nth-child(2) {
      z-index: 2;
    }
    &-text {
      font-size: 15px;
      line-height: 100%;
      color: #fff;
    }
  }
  .green {
    background: #AFC82E;
  }
  .gray {
    background: #4F4F4F;
  }
  .white {
    background: #fff;
    border: 1px solid #CCCCCC;
  }
}
.form {
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
  margin-top: 28px;
  &-wrap {
    max-width: 504px;
    background: #fff;
    border-radius: 32px;
    padding: 32px;
  }
  &-title {
    font-size: 24px;
    line-height: 115%;
    font-weight: 700;
  }
  &-group {
    position: relative;
    margin: 0;
    input {
      width: 100%;
      height: 58px;
      background: #fff;
      border: 1px solid #D4D4DD;
      border-radius: 12px;
      padding: 0 20px;
      font-size: 16px;
      line-height: 115%;
      color: #282827;
      &::placeholder {
        color: #7D8193;
      }
    }
    .error {
      display: none;
      font-size: 14px;
      line-height: 115%;
      color: #C82E65;
      margin-top: 8px;
    }
  }
  .active {
    input {
      border-color: #C82E65;
    }
    .error {
      display: block !important;
    }
    #btn-country {
      border-color: #C82E65 !important;
    }
    #phoneNumber {
      border-color: #C82E65 !important;
    }
  }
  .active2 {
    input {
      border-color: #AFC82E;
    }
    #btn-country {
      border-color: #AFC82E !important;
    }
    #phoneNumber {
      border-color: #AFC82E !important;
    }
  }
  &-btn {
    height: 64px;
    border-radius: 12px;
    padding: 5px 15px;
    margin-top: 8px;
    span {
      font-size: 20px;
      line-height: 120%;
    }
  }
  &-check {
    position: relative;
    margin-top: 8px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check-icon.svg");
      }
    }
    label {
      position: relative;
      display: block;
      padding-top: 5px;
      padding-left: 36px;
      padding-bottom: 5px;
      font-size: 12px;
      line-height: 120%;
      font-weight: 400;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: #464646;
        border-radius: 4px;
      }
      a {
        position: relative;
        z-index: 2;
        text-decoration: unset !important;
      }
      a:hover {
        color: #AFC82E;
      }
    }
  }
  .dropdown-menu {
    overflow-x: hidden;
  }
  #btn-country {
    height: 58px;
    background: transparent !important;
    background-color: transparent !important;
    border-color: #D4D4DD !important;
    border-radius: 12px;
    margin-right: 8px;
  }
  .btn-cc {
    font-size: 16px;
  }
  #phoneNumber {
    border-color: #D4D4DD !important;
    box-shadow: unset !important;
    border-radius: 12px;
  }
  .file {
    position: absolute;
    z-index: 3;
    top: 20px;
    right: 20px;
  }
  .input-file {
    position: absolute;
    z-index: 4;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    opacity: 0;
  }
  .file-label {
    width: 14px;
    img {
      width: 100%;
    }
  }
  .input-text {
    padding-right: 40px;
  }
  .country-numbers {
    width: 100%;
    display: flex;
    align-items: stretch;
    gap: 8px;
  }
  .select-wrap {
    position: relative;
    flex-shrink: 0;
  }
  .select-wrap--active {
    .select-body {
      transform: translateY(0);
      opacity: 1;
      pointer-events: auto;
    }
  }
  .select-head {
    width: 103px;
    height: 58px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    background: #fff;
    border: 1px solid #d4d4dd;
    border-radius: 12px;
    padding: 4px;
    cursor: pointer;
  }
  .select-current {
    .country {
      display: none;
    }
  }
  .select-arrow {
    width: 11px;
    svg {
      width: 100%;
      height: auto;
    }
  }
  .select-body {
    position: absolute;
    z-index: 5;
    top: calc(100% + 5px);
    left: 0;
    transform: translateY(-5px);
    width: auto;
    max-width: 250px;
    min-width: 250px;
    max-height: 250px;
    height: auto;
    background: #fff;
    border: 1px solid #d4d4dd;
    border-radius: 12px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.225s;
    overflow-y: auto;
    li {
      padding: 8px;
      cursor: pointer;
    }
  }
  .li--active {
    background: rgba(0, 0, 0, 0.05);
  }
  .country-group {
    display: flex;
    align-items: center;
    gap: 4px;
  }
  .flag {
    width: 29px;
    img {
      width: 100%;
    }
  }
  .country-subgroup {
    display: flex;
    align-items: center;
    gap: 4px;
  }
  .country {
    font-size: 16px;
    line-height: 120%;
  }
  .country-code {
    font-size: 16px;
    line-height: 120%;
    color: #7D8193;
  }
  .country-numbers {
    .error {
      margin-left: -111px;
    }
  }
  .country-numbers__inputs {
    width: 100%;
  }
  .tab__content2 {
    width: 100%;
    display: none;
  }
  .tab__content2--active {
    display: block;
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(6.1px);
  padding: 0 16px !important;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: calc(100% - 32px);
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
    }
  }
  &__close {
    position: absolute;
    z-index: 2;
    top: 24px;
    right: 24px;
    width: 24px;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
}
.modal-form {
  .modal__dialog {
    max-width: 1255px;
    background: #fff;
    border-radius: 48px;
    padding: 48px;
  }
  .connection__inner {
    background-image: unset;
    border-radius: 0;
    padding: 0;
  }
  .connection__desc {
    max-width: 597px;
    justify-content: start;
    gap: 40px;
    padding-bottom: 0;
  }
  .connection__title {
    max-width: 100%;
    color: #282827;
  }
  .connection__text {
    max-width: 100%;
    color: #282827;
  }
  .form-wrap {
    flex-shrink: 0;
    background: #F9F9F9;
  }
}
.modal-form2 {
  .modal__dialog {
    max-width: 504px;
  }
  .form-wrap {
    max-width: 100%;
    min-height: 614px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 15px;
  }
  .form-group2 {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 15px;
  }
  .form-icon {
    width: 40px;
    img {
      width: 100%;
    }
  }
  .form-subtitle {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    color: #AFC82E;
  }
  .form {
    margin-top: 0;
  }
  .modal__close {
    top: -36px;
    right: 0;
  }
}
.modal-form3 {
  .modal__dialog {
    max-width: 756px;
  }
  .modal__close {
    top: -36px;
    right: 0;
  }
  .form-group2 {
    min-height: 333px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 15px;
    border: 1px dashed #AFC82E;
    border-radius: 32px;
  }
  .form-icon {
    width: 40px;
    img {
      width: 100%;
    }
  }
  .form-subtitle {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    color: #AFC82E;
  }
}
.cookie {
  position: fixed;
  z-index: 3;
  left: 50%;
  bottom: 30px;
  transform: translate(-50%, 0);
  max-width: 1490px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  background: #fff;
  border-radius: 16px;
  padding: 24px;
  &__text {
    font-size: 24px;
    line-height: 120%;
    color: #AFC82E;
  }
  &__btn {
    height: 64px;
    border-radius: 12px;
    padding: 5px 68.5px;
    span {
      font-size: 20px;
    }
  }
}
.breadcrumbs {
  padding: 48px 0 24px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px;
  }
  &__item {
    position: relative;
    font-size: 20px;
    line-height: 140%;
    text-decoration: unset !important;
    color: #7A7A7A;
    &:not(:last-child) {
      padding-right: 16px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 8px;
        height: 8px;
        background: #7A7A7A;
        border-radius: 50%;
        pointer-events: none;
      }
    }
    &:hover {
      color: #282827;
    }
  }
  &__item--active {
    color: #282827;
  }
}
.tags {
  &__slider {
    .swiper {
      position: relative;
      z-index: 2;
      overflow: unset;
    }
    .tab2:hover {
      background: #282827;
      span {
        color: #fff;
      }
    }
    .tab2--active {
      background: #282827;
      span {
        color: #fff;
      }
    }
  }
  &__slider--active {
    position: fixed;
    z-index: 5;
    top: 0;
    padding: 32px 0;
    transition: all 0.3s;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 20000px;
      height: 100%;
      background: #fff;
      pointer-events: none;
    }
  }
  &__btn {
    position: relative;
    height: 48px;
    display: none;
    justify-content: space-between;
    align-items: center;
    background: #282827;
    border-radius: 12px;
    gap: 10px;
    padding: 10px 38px 10px 20px;
    margin-top: 32px;
    cursor: pointer;
  }
  &__btn-title {
    display: flex;
    align-items: center;
    gap: 10px;
    i {
      width: 24px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 14px;
      line-height: 140%;
      color: #fff;
    }
  }
  &__btn-arrow {
    position: absolute;
    top: 50%;
    right: 27px;
    transform: translate(0, -50%);
    width: 8px;
    img {
      width: 100%;
    }
  }
  &__close {
    display: none;
  }
}
.tag {
  height: 52px;
  width: auto !important;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #282827;
  border-radius: 57px;
  padding: 5px 18.5px;
  text-decoration: unset !important;
  span {
    font-size: 20px;
    line-height: 140%;
    color: #282827;
  }
}
.show-more {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  text-decoration: unset !important;
  span {
    font-size: 18px;
    line-height: 140%;
    color: #AFC82E;
  }
  i {
    width: 12px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    img {
      width: 100%;
    }
  }
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
  }
  &__prev,
  &__prev2,
  &__next,
  &__next2,
  &__item {
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border: 1px solid #F1F1F1;
    border-radius: 8px;
    font-size: 14px;
    line-height: 140%;
    text-decoration: unset !important;
    color: #333333;
    &:hover {
      background: #AFC82E;
      color: #fff;
      svg path {
        fill: #fff;
      }
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__item--active {
    background: #AFC82E;
    color: #fff;
  }
}
.hover {
  &__slider {
    position: relative;
  }
  &__content {
    display: none !important;
  }
  &__content--active {
    display: block !important;
  }
  &__tab-head {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
  }
  &__tab {
    flex: 1 1 auto;
    background: rgba(0, 0, 0, 0);
  }
  &__pagination {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 5px;
    padding: 16px;
    &-bullet {
      width: 50px;
      height: 3px;
      display: block !important;
      background: #fff;
      border-radius: 42px;
      opacity: 50%;
    }
    .hover__content--active {
      opacity: 1;
    }
  }
}
.show-more {
  cursor: pointer;
  transition: all 0.3s;
  span:nth-child(2) {
    display: none;
  }
  i {
    transition: all 0.3s;
  }
}
.show-more--active {
  span:nth-child(1) {
    display: none;
  }
  span:nth-child(2) {
    display: block;
  }
  i {
    transform: rotate(-180deg);
  }
}
