      input {
        height: 48px;
        padding: 0 16px;
        font-size: 14px;
      }
    }
    &-btn {
      height: 44px;
      margin-top: 0;
      span {
        font-size: 14px;
      }
    }
    &-check {
      margin-top: 0;
      label {
        padding-top: 0;
        padding-bottom: 0;
      }
    }
    #phoneNumber {
      height: 58px;
    }
    .btn-cc {
      font-size: 14px;
    }
    .file {
      top: 14px;
    }
    .select-head {
      height: 48px;
    }
  }
  .modal {
    padding: 0 4px !important;
    &__close {
      top: 12px;
      right: 12px;
    }
  }
  .modal-form {
    .modal__dialog {
      border-radius: 24px;
      padding: 20px;
    }
    .connection__inner {
      gap: 32px;
    }
    .connection__title {
      font-size: 24px;
    }
  }
  .modal-form2 {
    .modal__dialog {
      max-width: 335px;
    }
    .form-wrap {
      min-height: 467px;
    }
    .form-subtitle {
      font-size: 18px;
    }
  }
  .modal-form3 {
    .form-group2 {
      min-height: 137px;
      border-radius: 12px;
    }
    .form-subtitle {
      font-size: 18px;
    }
  }
  .breadcrumbs {
    padding: 24px 0;
    &__item {
      font-size: 14px;
      &:not(:last-child) {
        padding-right: 13px;
        &:after {
          width: 5px;
          height: 5px;
        }
      }
    }
  }
  .tags {
    &__slider {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #F9F9F9;
      padding: 80px 20px 40px 20px;
      margin-top: 0 !important;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        grid-template-columns: 100%;
        gap: 40px;
      }
      .tab2--active {
        background: transparent;
        padding-left: 30px;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 10px;
          height: 20px;
          background-image: url("../img/arrow-left.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        span {
          font-size: 24px !important;
          color: #AFC82E;
        }
      }
    }
    &__btn {
      display: flex;
    }
    &__close {
      position: absolute;
      top: 40px;
      right: 20px;
      width: 46.2px;
      height: 14px;
      display: block;
      span {
        position: absolute;
        left: 0;
        width: 100%;
        height: 2.8px;
        background: #AFC82E;
        transition: all 0.125s;
      }
      span:nth-child(1) {
        top: 5px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        bottom: 5px;
        transform: rotate(45deg);
      }
    }
  }
  .tag {
    position: relative;
    height: auto;
    justify-content: start;
    align-items: start;
    border: none;
    padding: 0;
    margin: 0 !important;
    span {
      font-family: 'mts-ultraextended';
      font-size: 20px;
      text-transform: uppercase;
    }
  }
  .pagination {
    &__prev,
    &__prev2,
    &__next,
    &__next2,
    &__item {
      width: 32.78px;
      height: 32.78px;
    }
  }
}

@media screen and (max-width: 374px) {
  .pagination {
    &__prev,
    &__prev2,
    &__next,
    &__next2,
    &__item {
      width: 32px;
      height: 32px;
    }
    &__prev2,
    &__next2 {
      display: none;
    }
  }
}

@media screen and (max-width: 359px) {
  .modal-form {
    .connection__title {
      font-size: 20px;
    }
  }
}