.footer {
  background-image: url("../img/footer-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #fff;
  &__subtitle {
    font-size: 18px;
    line-height: 140%;
    margin-top: 30px;
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-top: 30px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
    &:hover {
      background: #fff;
      svg path {
        fill: #282827;
      }
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__items {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 32px 24px;
  }
  &__item {
    &:last-child {
      grid-column: 2/3;
    }
    &-title {
      font-size: 18px;
      line-height: 140%;
      color: #777777;
    }
    &-text {
      display: block;
      font-size: 20px;
      line-height: 140%;
      text-decoration: unset !important;
      margin-top: 12px;
      &:hover {
        color: #AFC82E;
      }
    }
  }
  &__cities {
    display: inline-flex;
    align-items: center;
    gap: 16px;
  }
  &__city {
    font-size: 18px;
    line-height: 140%;
    text-decoration: unset !important;
    color: #777777;
    cursor: pointer;
    &:hover {
      color: #777777;
    }
  }
  .tab2--active {
    color: #fff !important;
    &:hover {
      color: #fff !important;
    }
  }
  &__text {
    font-size: 18px;
    line-height: 140%;
    margin-top: 16px;
  }
  &__socials {
    display: flex;
    align-items: start;
    gap: 12px 24px;
    margin-top: 64px;
  }
  &__social {
    width: 48px;
    img {
      width: 100%;
    }
  }
  &__list {
    max-width: 279px;
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    li a {
      font-size: 20px;
      line-height: 140%;
      text-decoration: unset !important;
      &:hover {
        color: #AFC82E;
      }
    }
  }
  &__text2 {
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    gap: 40px;
    margin-top: 80px;
    p {
      font-size: 20px;
      line-height: 140%;
    }
    p a:hover {
      color: #AFC82E;
    }
  }
  .tab__content2 {
    display: none;
  }
  .tab__content2--active {
    display: block;
  }
}
.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: 140px 0 80px;
  &__left {
    max-width: 751px;
  }
  &__right {
    max-width: 584px;
    width: 100%;
  }
}
.footer-bottom {
  padding: 80px 0 140px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 48px 24px;
  }
  &__left {
    max-width: 583px;
  }
  &__right {
    max-width: 883px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
  }
}

@media screen and (max-width: 1600px) {
  .footer-bottom {
    &__right {
      max-width: 584px;
      flex-shrink: 0;
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer-top {
    padding-top: 80px;
    &__right {
      width: auto;
      flex-shrink: 0;
    }
  }
  .footer-bottom {
    padding-bottom: 80px;
    &__right {
      max-width: 470px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__list {
      width: auto;
    }
  }
  .footer-bottom {
    &__right {
      width: auto;
      gap: 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__socials {
      margin-top: 48px;
    }
    &__list {
      width: 100%;
      li a {
        font-size: 18px;
      }
    }
    &__text2 {
      margin-top: 48px;
      p {
        font-size: 18px;
      }
    }
  }
  .footer-top {
    flex-direction: column;
    padding-bottom: 48px;
    &__left {
      max-width: 100%;
    }
    &__right {
      max-width: 100%;
      width: 100%;
    }
  }
  .footer-bottom {
    padding-top: 48px;
    &__wrap {
      flex-direction: column;
      gap: 48px;
    }
    &__right {
      max-width: 100%;
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__items {
      grid-template-columns: 100%;
      gap: 24px;
    }
    &__item {
      &:last-child {
        grid-column: unset;
      }
    }
  }
  .footer-top {
    padding-top: 64px;
  }
  .footer-bottom {
    padding-bottom: 64px;
    &__right {
      display: none;
    }
  }
}

@media screen and (max-width: 374px) {
  .footer {
    &__socials {
      flex-wrap: wrap;
    }
  }
}