.services-main {
  padding-bottom: 24px;
  &__inner {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 32px / 2));
    gap: 32px;
    border-bottom: 1px solid #D9D9D9;
    padding-bottom: 48px;
  }
  &__left {
    display: flex;
    flex-direction: column;
    background: #fff;
    border-radius: 48px;
    padding: 40px;
  }
  &__right {
    position: relative;
  }
  &__title {
    color: #AFC82E;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 32px;
    margin-bottom: auto;
    p {
      font-size: 20px;
      line-height: 140%;
      color: #7A7A7A;
    }
    strong {
      color: #282827;
    }
  }
  &__btns {
    margin-top: 24px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
  }
  &__img {
    position: relative;
    padding-bottom: 85%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 32px;
    }
  }
  &__items {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 8px;
    padding: 40px;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 37px;
    padding: 8px 16px;
    font-size: 16px;
    line-height: 140%;
  }
}
.services-main--2 {
  padding-bottom: 70px;
  .services-main__inner {
    border-bottom: unset;
    padding-bottom: 0;
  }
}
.services2 {
  padding: 24px 0 140px;
  .projects__cards {
    &:first-child {
      border-top: unset;
      padding-top: 0;
    }
    &:last-child {
      border: none;
      padding-bottom: 0;
    }
    &:nth-child(3) {
      border: none;
      padding-bottom: 0;
    }
    &:nth-child(3) ~ .projects__cards {
      display: none;
    }
  }
  .all-cards--active {
    .projects__cards {
      display: grid !important;
    }
    .projects__cards:nth-child(3) {
      border-bottom: 1px solid #d9d9d9;
      padding-bottom: 36px;
    }
  }
  .project-card {
    &__btns {
      margin-top: 32px;
    }
    &__group {
      margin-top: 0;
    }
    &__img {
      padding-bottom: 51.1%;
    }
    .text-overflow--active {
      max-height: 100% !important;
    }
  }
  .show-more {
    margin-top: 48px;
  }
}
.services3 {
  padding: 70px 0;
  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, calc(50%));
    margin-top: 48px;
  }
  .projects__cards {
    position: relative;
    display: grid !important;
    grid-template-columns: repeat(2, calc(50% - 32px / 2));
    gap: 32px;
    padding: 32px 0;
    &:nth-child(1) {
      border-top: 1px solid #D9D9D9;
      padding-top: 32px;
    }
    &:nth-child(2) {
      border-top: 1px solid #D9D9D9;
    }
    &:nth-child(3) {
      border-bottom: 1px solid #D9D9D9;
      padding-bottom: 32px;
    }
    &:nth-child(odd) {
      padding-right: 32px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 1px;
        height: calc(100% - 64px);
        background: #D9D9D9;
      }
    }
    &:nth-child(even) {
      padding-left: 32px;
    }
    &:last-child {
      border-bottom: 1px solid #D9D9D9;
      padding-bottom: 32px;
    }
  }
  .project-card {
    &:nth-child(1) {
      grid-column: 1/3;
    }
    &__img {
      padding-bottom: 68.3%;
    }
  }
  &__btns {
    justify-content: center;
    margin-top: 48px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
  }
}
.info {
  padding: 70px 0;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    background: #fff;
    border-radius: 32px;
    padding: 32px;
    margin-top: 64px;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    background: #F9F9F9;
    border-radius: 16px;
    padding: 27px 24px;
    &-text {
      font-size: 18px;
      line-height: 140%;
      &:nth-child(2) {
        max-width: 550px;
        width: 100%;
      }
    }
  }
}
.info2 {
  padding: 70px 0 140px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
  }
  &__left {
    width: calc(100% - 454px);
    background: #fff;
    border-radius: 48px;
    padding: 40px;
    .info2__title {
      color: #AFC82E;
    }
  }
  &__right {
    width: 430px;
    background: #fff;
    border-radius: 48px;
    padding: 40px;
  }
  &__title--mob {
    display: none;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 32px;
    p {
      font-size: 20px;
      line-height: 140%;
      color: #7A7A7A;
    }
    strong {
      font-weight: 400;
      color: #282827;
    }
  }
  &__btns {
    margin-top: 32px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
    span {
      font-size: 20px;
    }
  }
  &__btn2 {
    width: 60px;
    height: 60px;
    flex-shrink: 0;
  }
  &__links {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 32px;
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 10px;
    text-decoration: unset !important;
    i {
      width: 20px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 20px;
      line-height: 140%;
      color: #AFC82E;
    }
  }
}

@media screen and (max-width: 1400px) {
  .info {
    &__item {
      &-text {
        &:nth-child(2) {
          max-width: 400px;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .services-main {
    &__inner {
      grid-template-columns: 100%;
    }
  }
  .services2 {
    .projects__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
      padding: 32px 0;
    }
    .project-card {
      &:nth-child(1) {
        grid-column: 1/3;
      }
      &__img {
        min-height: 267px;
      }
    }
  }
  .services3 {
    &__wrap {
      grid-template-columns: 100%;
    }
    .projects__cards {
      padding-left: 0 !important;
      padding-right: 0 !important;
      &:last-child {
        border-bottom: unset;
        padding-bottom: 0;
      }
      &:after {
        display: none;
      }
    }
  }
  .info2 {
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .services-main {
    padding-bottom: 16px;
    &__left {
      padding: 32px;
    }
    &__items {
      padding: 24px;
    }
  }
  .services-main--2 {
    padding-bottom: 24px;
  }
  .services2 {
    padding: 16px 0 80px;
    .show-more {
      margin-top: 32px;
    }
  }
  .services3 {
    padding: 24px 0 40px;
    &__wrap {
      margin-top: 32px;
    }
    &__btns {
      margin-top: 32px;
    }
    &__btn {
      flex-grow: 1;
    }
  }
  .info {
    padding: 40px 0;
    &__items {
      gap: 24px;
      padding: 24px;
      margin-top: 48px;
    }
    &__item {
      flex-direction: column;
      align-items: start;
      padding: 16px;
      &-text {
        &:nth-child(2) {
          max-width: 100%;
        }
      }
    }
  }
  .info2 {
    padding: 40px 0 80px;
  }
}

@media screen and (max-width: 767px) {
  .services2 {
    .projects__cards {
      grid-template-columns: 100%;
    }
    .project-card {
      grid-column: unset !important;
      &:last-child {
        order: 1;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .services-main {
    &__inner {
      padding-bottom: 32px;
    }
    &__left {
      border-radius: 32px;
      padding: 24px;
    }
    &__title {
      font-size: 20px;
    }
    &__text {
      margin-top: 24px;
      p {
        font-size: 14px;
      }
    }
    &__btn {
      height: 44px;
      padding-left: 19px;
      padding-right: 19px;
      span {
        font-size: 14px;
      }
    }
    &__btn2 {
      width: 44px;
      height: 44px;
      svg {
        width: 18px;
        height: auto;
      }
    }
    &__items {
      display: none;
    }
    &__img {
      min-height: 290px;
      img {
        border-radius: 24px;
      }
    }
  }
  .services-main--2 {
    padding-bottom: 16px;
  }
  .services2 {
    padding: 16px 0 64px;
    .project-card {
      &__img {
        min-height: 201px;
      }
    }
  }
  .services3 {
    padding: 16px 0 32px;
    .project-card {
      &__img {
        min-height: 240px;
      }
      &__title {
        font-size: 18px;
      }
    }
    &__btn {
      height: 44px;
      span {
        font-size: 14px;
      }
    }
    &__btn2 {
      width: 44px;
      height: 44px;
      svg {
        width: 18px;
        height: auto;
      }
    }
  }
  .info {
    padding: 32px 0;
    &__items {
      padding: 16px;
      margin-top: 24px;
    }
    &__item {
      &-text {
        font-size: 14px;
      }
    }
  }
  .info2 {
    padding: 32px 0 64px;
    &__left,
    &__right {
      border-radius: 24px;
      padding: 16px;
    }
    &__right {
      .info2__title {
        display: none;
      }
      .info2__title--mob {
        display: block;
      }
    }
    &__title {
      font-size: 24px;
    }
    &__text {
      gap: 12px;
      margin-top: 16px;
      p {
        font-size: 14px;
      }
    }
    &__btns {
      margin-top: 16px;
    }
    &__btn {
      height: 44px;
      flex-grow: 1;
      span {
        font-size: 14px;
      }
    }
    &__btn2 {
      width: 44px;
      height: 44px;
      svg {
        width: 18px;
        height: auto;
      }
    }
    &__link {
      span {
        font-size: 14px;
      }
    }
  }
}